.gallery__card-list {
  list-style: none;
  display: grid;
  grid-template-columns: repeat(auto-fit, 282px);
  gap: 20px;
  margin: 0;
  padding: 0;
  justify-content: center;
}

@media screen and (min-width: 1024px) {
  .gallery__card-list {
    gap: 20px 17px;
    justify-content: start;
  }
}
