.pop-up {
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  visibility: hidden;
  transition: visibility 0s, opacity 0.3s linear;
  opacity: 0;
  z-index: 1;
}
